/* Estilos base para todas las tamaños de pantalla */
body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0;
    padding: 0;
}

/* Contenedor principal de la aplicación */
.App {
    width: 100%;
    min-height: 100vh;
    background-color: #ccc;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(./assets/multas.jpg);
    background-size: cover;
}

/* Sección heroica para información destacada */
.hero {
    background-color: #f8f8f8;
    padding: 2em;
    margin: 2em;
    text-align: center;
    border-radius: 2em;
}

.title {
    font-size: 3em;
    font-weight: 600;
    margin-bottom: 0.5em;
}

.text {
    font-size: 1.5em;
    font-weight: 400;
}

/* Estilos específicos para la zona principal */
.zone {
    background-color: #f8f8f8;
    max-width: 800px;
    width: 100%;
    margin: 2em;
    padding: 2em;
    border-radius: 10px;
}

.zone-container {
    display: flex;
    flex-direction: column;
}

/* Estilos para la sección izquierda de la zona */
.zone-izq {
    background-color: #25D366;
    padding: 2em;
    text-align: center;
    border-radius: 10px;
}

.zone-izq img {
    width: 100%;
    max-width: 200px;
}

.text2 {
    font-size: 1.2em;
    font-weight: 400;
    color: white;
    margin-top: 1em;
}

/* Estilos para la sección derecha de la zona (formulario) */
.zone-der {
    padding: 2em;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Estilos para el formulario de contacto */
form {
    width: 100%;
    max-width: 400px;
}

label {
    display: block;
    margin-bottom: 0.5em;
    font-size: 1.2em;
    color: #333;
    width: 100%;
}

input, textarea {
    width: 100%;
    padding: 0.5em;
    margin-bottom: 1em;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: none;
}

button {
    background-color: #25D366;
    color: white;
    padding: 0.5em 1em;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

/* Estilos para mensajes de validación */
p {
    font-size: 1.2em;
    color: green;
}

/* Media query para pantallas más pequeñas (ajustar el max-width según sea necesario) */
@media screen and (max-width: 768px) {
    .title {
        font-size: 2em;
        text-align: center;
    }

    .text,
    .text2 {
        font-size: 1em;
        
    }

    .zone {
        margin: 1em;
    width: 60vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    }

    .zone-izq,
    .zone-der {
        width: 70%;
        margin: 1em 0;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.2em;
    }

    .zone-der {
        padding: 1em;
        display: flex;
        align-items: center;
        justify-content: center;
    }

